<template>
	<div class="my-5">
		<div class="card mt-3" v-for="(task, index) in tasks" :key="index">		
			<div class="card-body py-2" >
				<font-awesome-icon @click="$emit('done', $event)" icon="fa-solid fa-check" class="me-2 text-success cursor-pointer icons" />	
					<span>{{ task }}</span>
				<font-awesome-icon @click="$emit('delete', $event)" icon="fa-solid fa-trash" class="float-end text-danger mt-1 icons" />
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'OnGoingListComponent',
	props: ['tasks'],
	emits: ['delete', 'done'],
}
</script>
