<template>
	<button :class="btnClass" @click="btnEvent">
		{{ btnName }}
	</button>
</template>

<script>
export default {
	name: "ButtonComponent",
	props: ['btnType', 'btnName', 'btnModifiers', 'btnEvent'],
	data() {
		return {
			btnClass: `btn btn-${this.btnType} ${this.btnModifiers}`,
		};
	},
}
</script>
