<template>
	<Navbar :count="stateTasks.length" @clearDoneTasks="clearDoneTasks" @clearAllTheTasks="clearAllTasks" />

	<Container class="mt-4" maxWidth="700">
		<AlertSuccess v-show="isAlert" />
		<AlertDanger v-show="isAlert" />
	
		<form @submit.prevent>
			<div class="input-group">
				<TaskBox class="task--input-box" v-model.lazy="task" />
				<Button btnType="primary" btnName="Add" :btnEvent="addTheTask" />
			</div>
		</form>
		
		<OnGoingList v-show="isAdded" :tasks="showUndoneTask()" @delete="deleteTask" @done="doneTask" />
		<FinishedList v-show="isDone" :doneTasks="showDoneTask()" @clear="clearDoneTask" />
	</Container>
</template>

<script>
import Container from './components/Container.vue';
import Navbar from './components/Navbar.vue';
import TaskBox from './components/TaskBox.vue';
import Button from './components/Button.vue';
import AlertSuccess from './components/AlertSuccess.vue';
import AlertDanger from './components/AlertDanger.vue';
import OnGoingList from './components/OnGoingList.vue';
import FinishedList from './components/FinishedList.vue';

export default {
  name: 'App',
	data() {
		return {
			isAlert: false,
			isDone: false,
			isAdded: false, 
			task: '',
			stateTasks: JSON.parse(localStorage.getItem('tasks')) || [],
			completeTasks: JSON.parse(localStorage.getItem('complete')) || [],
		};
	},

  components: {
		Navbar,
		TaskBox,
		Button,
		Container,
		AlertSuccess,
		AlertDanger,
		OnGoingList,
		FinishedList,
  },

	methods: {
		addTheTask() {
			if(!this.task){
				document.querySelector('.task--input-box').focus();
				return;
			}			

			let tasks = this.stateTasks;
			tasks.push(this.task);
			localStorage.setItem('tasks', JSON.stringify(tasks));

			this.isAdded = true;
			document.querySelector('.task--input-box').value = '';
		},

		deleteTask(event) {
			event.preventDefault();
			let index = this.stateTasks.indexOf(event.target.parentElement.previousSibling.textContent);
			this.stateTasks.splice(index, 1);
			localStorage.setItem('tasks', JSON.stringify(this.stateTasks));

			return this.stateTasks;
		},

		doneTask(event) {
			event.preventDefault();
			this.isDone = true;
			let index = event.target.parentElement.nextSibling.textContent;

			this.stateTasks.splice(index, 1);
			localStorage.setItem('tasks', JSON.stringify(this.stateTasks));

			this.completeTasks.push(index);
			
			localStorage.setItem('complete', JSON.stringify(this.completeTasks));
		},

		showUndoneTask() {
			if(this.isAdded){
				return this.stateTasks;
			}
			// for reloading
			if(localStorage.getItem('tasks')){
				this.isAdded = true;
				return JSON.parse(localStorage.getItem('tasks'));
			}
		},

		showDoneTask() {
			if(this.isDone) {
				return this.completeTasks
			}

			if(localStorage.getItem('complete')) {
				this.isDone = true;
				return JSON.parse(localStorage.getItem('complete'));
			}
		},

		clearDoneTask(event) {
			let index = event.target.parentElement.previousSibling.textContent;

			this.completeTasks.splice(index, 1);
			localStorage.setItem('complete', JSON.stringify(this.completeTasks));
		},
	
		clearDoneTasks() {
			this.completeTasks.length = 0;
			localStorage.removeItem('complete');
		},

		clearAllTasks() {
			this.stateTasks.length = 0;
			this.completeTasks.length = 0;
			localStorage.removeItem('tasks');
			localStorage.removeItem('complete');
		}
	},
}
</script>

<style>
.icons {
	cursor:pointer;
}
</style>
